import * as React from "react"
import withUtterances from "with-utterances"

import Bio from "./bio"

const BlogPostContent = ({ title, date, html }: BlogPostContentProps): JSX.Element => (
    <article className="blog-post-content">
        <header>
        <h1>{title}</h1>
        <small>{date}</small>
        </header>
        <section dangerouslySetInnerHTML={{ __html: html }} />
        <hr />
        <footer><Bio /></footer>
    </article>
)

// Only doing this because withUtterances expects the component to be a class.
class BlogPostContentClassWrapper extends React.Component<BlogPostContentProps> {
    public render(): JSX.Element {
        return <BlogPostContent {...this.props} />
    }
};

interface BlogPostContentProps {
    title: string,
    date: string,
    html: string,
    location: Location // Expected by withUtterances
}

export default withUtterances(
    BlogPostContentClassWrapper,
    "smparsons/smparsons.github.io",
    "github-dark"
)
